html, body {
  height: 100%;
  width: 100%;
  max-height: 100%;
  max-width: 100%;
  margin: 0;
  padding: 0; }

@font-face {
  font-family: Avenir;
  src: url('./fonts/AvenirLTStd-Roman.otf'); }
@font-face {
  font-family: Avenir;
  font-weight: 900;
  src: url('./fonts/AvenirLTStd-Black.otf'); }

body {
  background: #F0F3FF; }

#app {
  display: flex;
  flex-direction: column;
  margin: 80px auto 0 auto;
  width: 1440px;
  // height: 1218px
  background: #FFFFFF;
  box-shadow: 0px 16px 32px rgba(0, 0, 0, 0.12);
  border-radius: 16px;
 }  // padding-bottom: 80px

.container {
  margin: 100px 0 80px 100px;
  width: 1240px;
 }  // padding-bottom: 40px

.header {
  &__title {
    font-family: Avenir;
    font-style: normal;
    font-weight: 900;
    font-size: 48px;
    line-height: 66px;
    color: #2E282C;
    margin: 0; } }

.filter-list {
  display: flex;
  margin-top: 40px; }

.filter-item {
  display: flex;
  align-items: center;
  margin-right: 20px;
  &__title {
    font-family: Avenir;
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    color: #2E282C;
    margin-right: 20px; } }

.favorites {
  display: flex;
  align-items: center;
  &__title {
    font-family: Avenir;
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    margin-right: 20px; }
  &__checkbox {
    cursor: pointer;
    height: 24px;
    width: 24px;
    background: #fff;
    border-radius: 6px;
    border: 1px #444444 solid;
    &_disable {
      background: #fff; }
    &_active {
      background: #2E282C; } } }

.item-selector {
  display: flex;
  position: absolute;
  min-width: 150px;
  max-width: 250px;
  align-items: center;
  height: 50px;
  border: 1px solid #444444;
  box-sizing: border-box;
  border-radius: 6px;
  position: relative;
  &__title {
    font-family: Avenir;
    font-style: normal;
    font-weight: 500;
    font-size: 24px;
    color: #2E282C;
    margin: 0 22px 0 22px;
    min-width: 130px;
    max-width: 230px;
    overflow: hidden;
    white-space: nowrap; }
  &__arrow {
    position: relative;
    right: 0;
    width: 28px;
    cursor: pointer;
    height: 100%;
    display: flex;
    align-items: center; }
  &__list {
    position: absolute;
    top: 50px;
    width: 100%;
    z-index: 2;
    overflow-y: scroll;
    background: rgba(1, 1, 1, 0.2);
    max-height: 300px;
    border-radius: 0 0 6px 6px;
    display: none;
    &_disable {
      display: none; }
    &_active {
      display: flex;
      flex-direction: column; } }
  &__item {
    height: 40px;
    min-height: 40px;

    margin-top: 2px;
    display: flex;
    align-items: center;
    cursor: pointer;
    background: #f3f3f3;
    border-radius: 5px;
    font-family: Avenir;
    font-style: normal;
    font-weight: 500;
    &-text {
      margin-left: 22px; }
    &:hover {
      background: #e7e7e7; } } }

.event-card {
  &-list {
    margin-top: 42px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-auto-rows: 400px;
    row-gap: 40px;
    column-gap: 40px; }
  &-item {
    width: 100%;
    height: 100%;
    position: relative;
    display: grid;
    grid-template-rows: 40px 80px auto 95px;
    grid-template-columns: 40px 80px auto 15px 40px;
    overflow: hidden;
    border-radius: 6px;
    &__image {
      height: 100%;
      width: 100%;
      position: absolute;
      // z-index: -2
      img {
        height: 100%;
        width: 100%; } }
    &__overlay {
      background: rgba(0, 0, 0, 0.2);
      position: absolute;
      // z-index: -1
      width: 100%;
      height: 100%; }
    &__date {
      width: 80px;
      height: 80px;
      border: 2px solid #FFFFFF;
      box-sizing: border-box;
      border-radius: 6px;
      display: flex;
      align-items: center;
      justify-content: center;
      font-family: Avenir;
      font-weight: normal;
      font-size: 48px;
      color: #FFFFFF;
      // z-index: 2
      position: relative;
      grid-column: 2;
      grid-row: 2; }
    &__title {
      font-family: Avenir;
      font-style: normal;
      font-weight: 900;
      color: #FFFFFF;
      position: relative;
      font-size: 48px;
      grid-column: 2 / 4;
      grid-row: 4;
      margin: 0;
      line-height: 0.8; }
    &__bookmark {
      position: relative;
      grid-column: 4;
      grid-row: 2;
      cursor: pointer; } } }

@media ( max-width: 1480px) {
  #app {
    width: 100%;
    height: auto;
    margin-top: 0;
    border-radius: 0; }

  .container {
    margin: 0 auto;
    margin-top: 40px; } }

@media ( max-width: 1290px) {
  .container {
    width: 960px; }

  .header {
    &__title {
      font-size: 42px; } }

  .event-card {
    &-list {
      margin-top: 20px;
      grid-template-columns: 1fr 1fr;
      grid-auto-rows: 300px;
      row-gap: 20px;
      column-gap: 20px; }
    &-item {
      grid-template-rows: 20px 60px auto 95px;
      grid-template-columns: 20px 60px auto 15px 20px;
      &__date {
        width: 60px;
        height: 60px;
        font-size: 32px; }
      &__title {
        font-size: 36px; } } } }

@media ( max-width: 1080px) {
  .container {
    width: 768px; }

  .filter-list {
    display: flex;
    margin-top: 10px; }

  .header {
    &__title {
      font-size: 32px; } }

  .event-card {
    &-list {
      margin-top: 20px;
      grid-template-columns: 1fr 1fr;
      grid-auto-rows: 200px;
      row-gap: 10px;
      column-gap: 10px; }
    &-item {
      grid-template-rows: 20px 40px auto 55px;
      grid-template-columns: 20px 40px auto 15px 20px;
      &__date {
        width: 40px;
        height: 40px;
        font-size: 20px; }
      &__title {
        font-size: 32px; } } }

  .item-selector {
    height: 40px;
    &__title {
      font-size: 22px;
      color: #2E282C;
      margin: 0 22px 0 22px; }
    &__list {
      position: absolute;
      top: 40px; } }
  .filter-item {

    &__title {
      font-size: 22px; } } }

@media ( max-width:  778px) {
  .container {
    width: 576px; }
  .event-card {
    &-list {
      margin-top: 10px;
      grid-template-columns: 1fr 1fr;
      grid-auto-rows: 150px;
      row-gap: 10px;
      column-gap: 10px; }
    &-item {
      grid-template-rows: 10px 40px auto 45px;
      grid-template-columns: 10px 40px auto 15px 10px;
      &__date {
        width: 40px;
        height: 40px;
        font-size: 20px; }
      &__title {
        font-size: 26px; } } }

  .item-selector {
    height: 32px;
    &__title {
      font-size: 18px;
      color: #2E282C;
      margin: 0 22px 0 22px; }
    &__list {
      top: 32px; } }
  .filter-item {
    &__title {
      font-size: 18px; } } }

@media ( max-width:  778px) {
  .container {
    margin-top: 0px; }

  .filter-list {
    flex-direction: column; }

  .filter-item {
    margin: 0 0 20px 0;
    &__title {
      margin-right: auto; } }

  .favorites {
    &__title {
      font-size: 18px;
      margin-right: auto; } }

  .item-selector {
    max-width: 400px;
    min-width: 400px;
    &__title {
      min-width: 130px;
      max-width: 330px; }
    &__arrow {
      position: absolute;
      right: 0; } } }

@media ( max-width:  594px) {
  .container {
    width: 375px; }

  .event-card {
    &-list {
      grid-template-columns: 1fr;
      grid-auto-rows: 180px; } }

  .item-selector {
    max-width: 200px;
    min-width: 200px;
    &__title {
      min-width: 130px;
      max-width: 330px; }
    &__arrow {
      position: absolute;
      right: 0; } } }

@media ( max-width:  375px) {
  .container {
    width: 320px; } }
